import React from "react";
import {
  ConfigurateButton,
  HeaderContent,
  ScrollDown,
  SubText,
  Title,
  HeaderWrap,
} from "../elements/Header";

//ASSETS
import arrow from "../assets/konfarrow.svg";

const Header = () => {
  return (
    <>
      <HeaderContent>
        <ScrollDown />
        <HeaderWrap>
        <Title>
          Witaj <br /> w mojej kuni.
        </Title>
        <SubText>
          Stworz internetow dusz twojej firmy i zadbam o jej pozycje w
          cyfrowym wiecie.
        </SubText>
        <ConfigurateButton>
          <span>Stwrz swj wasny projekt</span>
          <span id="arrowwrap">
            <img src={arrow} alt="arrow" />
          </span>
        </ConfigurateButton>
        </HeaderWrap>
      </HeaderContent>
    </>
  );
};

export default Header;
