import React from 'react'
import { Label, LabelsBox, LeftBox, OfferBox, OfferTitle, SectionOneContent, SectionOneWrapper, SubText, TextOne, TextOneBox, Title } from '../elements/SectionOne'

const SectionOne = () => {

  const labels = ['Stworz dla Ciebie profesjonaln stron internetow', 'Przygotuj projekt strony WWW', 'Roczna gwarancja', 'Wszystko od A - Z', 'Bezpatna wycena indywidualna']

  return (
    <SectionOneWrapper>
      <SectionOneContent>
        <Title>OFERTA</Title>
        <OfferBox>
        <LeftBox>
          <OfferTitle>
            Strony internetowe 
            <br /> oraz blogi
          </OfferTitle>
          <TextOneBox>
            <TextOne>STRONY WIZYTWKOWE</TextOne>
            <TextOne>SPA</TextOne>
            <TextOne>BLOGI</TextOne>
          </TextOneBox>
          <SubText>
          Buduj strony internetowe dla maych i wikszych biznesw. Od projektu graficznego, przez wdroenie strony a przez utrzymywanie jej za ciebie. 
          </SubText>
          <LabelsBox>
            {labels.map(label => <Label>{label}</Label>)}
          </LabelsBox>
        </LeftBox>
        </OfferBox>
      </SectionOneContent>
    </SectionOneWrapper>
  )
}

export default SectionOne